@import url(https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap);

.App {
  background: #fbfbfc;
  position: fixed;
	/* width: 100%; */
  height:100%;
  
  font-family: 'Sulphur Point', sans-serif;
  font-size: 0.9rem;
  color: #fefefe;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media screen and (min-width: 601px) {
  h3 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: center;
    position: absolute;
    width: 40%;
    left: 50%;
    top: 50%;
    font-size: 1.0em;
    color: #d8d6d6;
    transform: translate(-50%, -50%);
  }
} 

@media screen and (max-width: 600px) {
  h3 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: center;
    position: absolute;
    width: 30%;
    left: 50%;
    top: 50%;
    font-size: 10px;
    color: #d8d6d6;
    transform: translate(-50%, -50%);
  }
}
